.ServiceDescription h2 {
    font-weight: lighter;

}

.ServiceDescription hr {
    width: 80%;
    margin: auto; 
    margin-bottom: 30px;
}

.ServiceDescriptionMenu {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 8px;
}

.ServiceDescriptionMenu div {
    cursor: pointer;
    width: 16.66%;
    margin:0;
    height: 46px;
    line-height: 46px;
    background-color: #999999;
    margin-right: 4px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.ServiceDescriptionMenu div.activeMark{
    background-color: #eaeae1;
    border-top: 1px solid #999999;
    border-left: 1px solid #999999;
    border-right: 1px solid #999999;
}
.MenuDetails{
    min-height: 100vh;
}
.MenuDetails div article{
    padding: 30px 30px;
}
.MenuDetails div article:nth-child(even){
    background-color: rgb(242, 242, 242,0.8)
}

.MenuDetails div article p{
    text-align: left;
    line-height: 1.8em;;
     
}