.ServiceSideMenu{
    margin-top: 72px;
    position: fixed;
    top: -20px;
    left: 0;
    height: 100%;
    min-height: 100vh;
    width: 25%;
    background-color: #cccccc;
    padding-top: 12px; 
}

.ServiceSideMenu h2{
    font-weight: lighter;
}

.ServiceSideMenu div{
    text-decoration: none;
    display: block;
    color:  #1a1a1a;
    margin-bottom: 1px;
    text-align: left;
    padding:13px 0 13px 20px;
    background-color: #999999;
    transition: background-color 0.2s ease-in;
    cursor: pointer;

}

/* .ServiceSideMenu  div:hover
{
    background-color: #eaeae1;
} */
.ServiceSideMenu div.activeMark{
    background-color: #eaeae1;
}
.ServiceSideMenu hr{
    margin-bottom: 20px;
}