.ServiceCard{
    background-color: #1e1e15;
    width: 100%;
    margin-bottom: 25px;
}

.ServiceCard li{
    list-style: none;
    color: #f5f5f0;
    cursor: pointer;
    margin-bottom: 15px;
}



.Heading{
    color: #f5f5f0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    padding: 0px 15px;
}

.Heading div{
    line-height: 70px;
}

.ServiceCard p {
    display: inline-block;
    width: 60%;
    text-align: left;
    margin-top: 0px;
    margin-bottom: 0px;
}

.Display {
    display: block;
    padding: 0px;
}

.NoDisplay{
    display:none;
}

.NoShowServiceCard{
    height: 70px;
}

.ServiceCard hr{
    width: 80%;
    margin: auto;
}

@media(min-width: 650px){
    .ServiceCard {
        width: 40%;
    }
}
