
.Nav {
    height: 40px;
    width: 100%;
    background-color:#1e1e15;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    position: relative;

}

.Sidebar{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    width:20%;
}

.Sidebar div{
    width: 30px;
    height: 2px;
    background-color:  #f5f5f0;
    margin-bottom: 3px;
}

.Nav p {
    margin-left: 18px;
    color: #f5f5f0;
}

.Nav ul{
    display: none;
}
.Layout footer{
    width: 100%;
    height: 90px;
    background-color:#1e1e15;
}

.Contact{
    display:none;
}

.SidebarMenu{
    width: 100%;
    margin: auto;
}
.SidebarMenu ul{
    list-style: none;
    width:100%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 0px;
}

.SidebarMenu a{
    text-decoration: none;
    color:#1e1e15;
}

.ListStyle{
    display: flex;
    justify-content:center;
    height: 100%;
    
}
.ServiceMenu {
    position: relative;
}
.ServiceMenuNotDisplay {
    display:none;
}
.ServiceMenuDisplay {
    display: block;
    position: absolute;
    top: 30px;
    left: 50%;
    z-index: 90;
    background-color:#d6d6c2;
    transform: translateX(-50%);
    width: 100%;
    margin: auto;
    padding-top: 15px;
    padding-bottom: 15px;
}
.ServiceMenu > div > div {
    width: 90%;
    margin: auto;
} 
@media(min-width: 650px){

    .Nav {
        height: 56px;
        padding: 0;
        position: fixed;
        z-index: 100;
        top:0;
    }

    .Sidebar{
        display:none;
    }

    .ListStyle{
        align-items: center;
        
    }

    .ListStyle div{
        margin-left: 8px;
    }
    
    .ServiceList {
        display: none;
        position: absolute;
        left:-200px;
        top:56px;
        z-index: 50;
        background-color: #1e1e15;
        color: #f5f5f0;
        padding: 30px;
        width: 500px;
        padding-left: 0;
        padding-right: 0px;
    }

    .ServiceList > div > div{
        background-color: #1e1e15;
        color: #f5f5f0;
        height: 30px;
        line-height: 32px;
        transition: all 0.2s ease-in;
    }
    
    .Nav ul.MenuList {
        list-style: none;
        display: flex;
        justify-content: space-around;
        align-items: center;
        box-sizing: border-box;
        height: 100%;
        text-align: center;
        margin: 0;
        padding: 0;
    }
    
    .Nav ul.MenuList li{
        height: 100%;
        display: block;
        margin-right: 18px;
        
    }
    
    .Nav ul.MenuList li a {
        text-decoration: none;
        display: block;
        box-sizing: border-box;
        height: 100%;
        padding: 20px;
        color: #f5f5f0;
        transition: all 0.2s ease-in;
    }
    
    .Nav ul.MenuList li:nth-child(even){
        background-color: #f5f5f0;
        width: 1px;
        height: 80%;
    }
    
    .Nav ul.MenuList li a:hover, 
    .Nav ul.MenuList li a:active {
        background-color: #f5f5f0;
        color: #1e1e15
    }
    
    .Nav ul.MenuList li:nth-child(5) a:hover .ServiceList{
        display: flex;
        justify-content: space-around;
    }
    
    .ServiceList > div > div:hover{
        background-color: #f5f5f0;
        color: #1e1e15
    }

    .Layout footer{
        height: 120px;
        position: absolute;
        z-index: 30;
    }

    .Contact {
        display: block;
        position: fixed;
        bottom: 0;
        right: 10px;
        background-color: #ff6633;
        width: 120px;
        height: 30px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        line-height: 30px;
        font-weight: bold;
    }

    .Contact a{
        text-decoration: none;
        color: #f5f5f0;
    }

    
}
